.App {
	background: linear-gradient(-45deg, #eeaa52, #b3255c, #2080a3, #1da988);
	background-size: 400% 400%;
	animation: gradient 7s ease infinite;
	height: 100vh;
}

.centered {
  display: "flex";
  flex-direction: "column"; 
  justify-content: "center";
  align-items: "center";
  height: "100vh"; 
  text-align: "center";
}

.fade-in-image {
  width: 150px; /* Adjust as needed */
  opacity: 0;
  animation: fadeIn 2s ease-in forwards; /* 2s fade-in effect */
}

.custom-button {
  background-color: rgba(0, 0, 0, 0);
  color: white;
  border: 2px solid white;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s ease;
  font-family: cursive;
}

.good {
  opacity: 0;
  transform: translateY(20px); 
  animation: fadeUp 2s ease-out forwards;
  animation-delay: 35s;
}

.bad {
  opacity: 0;
  transform: translateY(20px); 
  animation: fadeUp 2s ease-out forwards;
  animation-delay: 36s;
}

@keyframes fadeUp {
  0% {
    opacity: 0;
    transform: translateY(20px); /* Initial position */
  }
  100% {
    opacity: 1;
    transform: translateY(0); /* End position (normal) */
  }
}


.good:hover {
  background-color: #14c92c;
}
.neutral:hover {
  background-color: #14c92c;
}

.bad:hover {
  background-color: #c91a14;
}

.button-align-styles {
  padding-top: 20px;
  gap: 20px;
  display: flex;
  justify-content: center;
}


.btn-4:hover {
  
  &:after {
    left: 120%;
    transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
  /* 25% {
    background-position: 50% 0%;
  } */
	50% {
		background-position: 100% 50%;
	}
  /* 75% {
    background-position: 50% 100%;
  } */
	100% {
		background-position: 0% 50%;
	}
}
